<template>
  <div
    v-click-outside="hideDropdown"
    class="dropdown"
    @click="showDropdown"
  >
    <div
      class="dropdown__label"
      @click.stop="toggleDropdown"
    >
      <slot name="label" />
    </div>
    <div
      v-show="isShow"
      ref="body"
      class="dropdown__body"
      :style="position"
    >
      <slot name="body" />
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from 'vue'
import clickOutside from '@/directives/clickOutside'
import { checkViewPort } from '@/utils/checkViewPort'

export default {
  name: 'Dropdown',
  directives: {
    clickOutside
  },
  setup () {
    const isShow = ref(false)
    const body = ref()
    const view = ref({})
    const position = computed(() => {
      // const { value } = view
      const styles = {}
      return styles
    })

    const toggleDropdown = () => {
      isShow.value = !isShow.value
    }
    const hideDropdown = () => {
      isShow.value = false
    }
    const showDropdown = () => {
      isShow.value = true
    }

    onMounted(() => {
      view.value = checkViewPort(body.value)
    })

    return {
      isShow,
      body,
      position,
      view,
      showDropdown,
      hideDropdown,
      toggleDropdown
    }
  }
}
</script>

<style lang="scss">
.dropdown {
  position: relative;
  display: inline-block;

  &__label {
    cursor: pointer;
  }
  &__body {
    @include position-align-center;
    top: calc(100% + 16px);
    z-index: 10;
  }
}
</style>
