<template>
  <div class="pagination">
    <div
      v-for="item in pageSlots"
      :key="item"
      class="pagination__page pagination__page-slots"
    >
      {{ item }}
    </div>
    <div class="pagination__page pagination__page-divider">
      ...
    </div>
    <div class="pagination__page pagination__page-last">
      {{ pagesCount }}
    </div>
    <div class="pagination__page pagination__page-next">
      Next Page <Icon name="arrow-right-full" />
    </div>

  </div>
</template>

<script>
import { computed } from 'vue'
import Icon from '@/components/Icon/Icon'

export default {
  name: 'Pagination',
  components: { Icon },
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      type: Number,
      default: 1
    },
    perPage: {
      type: Number,
      default: 100
    },
    totalCount: {
      type: Number,
      default: 100
    },
    slotsCount: {
      type: Number,
      default: 6
    }
  },
  setup (props) {
    const pagesCount = computed(() => Math.ceil(props.totalCount / props.perPage))
    const pageSlots = computed(() => {
      const arr = []
      for (let i = 0; i < props.slotsCount; i++) {
        console.log(i, props.modelValue)
        arr.push(props.modelValue + i)
      }
      return arr
    })
    return {
      pagesCount,
      pageSlots
    }
  }
}
</script>

