<template>
  <section class="grid-header">
    <div class="grid-header__filters">
      <Input
        v-model="textFilter"
        hide-shadow
        placeholder="Filter by ID, IP, or Email Address, Location, Network"
        class="grid-header__filter grid-header__filter-search"
      >
        <template #prepend>
          <Icon name="search" />
        </template>
      </Input>
      <DateRange
        v-model="dateRange"
        placeholder-from="From"
        placeholder-to="To"
        class="grid-header__filter grid-header__filter-date-range"
      />
      <div class="grid-header__filter grid-header__filter-crypto">
        Crypto <Icon name="arrow-down" />
      </div>
    </div>
    <div class="grid-header__settings">
      <RadioButtons
        v-model="status"
        :data="radioButtons"
      >
        <template #default="{ item }">
          {{ item.title }}
        </template>
      </RadioButtons>
      <div class="grid-header__settings-table settings-table">
        <Dropdown class="settings-table__count">
          <template #label>
            <span class="settings-table__label settings-table__count__label">
              Show on page: {{ perPage }}
              <Icon name="arrow-down" class="settings-table__count__label-icon" />
            </span>
          </template>
          <template #body>
            <Container class="settings-table__count__body">
              <div
                v-for="item in pageSettings"
                :key="item"
                class="settings-table__count__item"
                @click="changeItemsPerPage(item)"
              >
                {{ item }}
              </div>
            </Container>
          </template>
        </Dropdown>
        <Dropdown class="settings-table__cols">
          <template #label>
            <span class="settings-table__label settings-table__cols__label">
              <Icon name="settings" class="settings-table__cols__label-icon" />
              Columns
            </span>
          </template>
          <template #body>
            <Container class="settings-table__cols__body">
              <div
                v-for="item in columns"
                :key="item.id"
                class="settings-table__cols__item"
              >
                <Checkbox v-model="item.isShow" :label="item.title"/>
              </div>
            </Container>
          </template>
        </Dropdown>
      </div>
    </div>
  </section>
</template>

<script>
import { computed, reactive, ref } from 'vue'
import Input from '@/components/UIKit/Input'
import Icon from '@/components/Icon/Icon'
import DateRange from '@/components/UIKit/DateRange'
import RadioButtons from '@/components/UIKit/RadioButtons'
import Dropdown from '@/components/UIKit/Dropdown'
import Container from '@/components/Container'
import { useStore } from 'vuex'
import Checkbox from '@/components/UIKit/Checkbox'

export default {
  name: 'GridHeader',
  components: { Checkbox, Container, Dropdown, RadioButtons, DateRange, Icon, Input },
  setup () {
    const store = useStore()
    const columns = computed(() => store.getters['swaps/getColumns'])

    const textFilter = ref('')
    const status = ref(null)
    const perPage = ref(100)
    const dateRange = ref({
      from: '',
      to: ''
    })
    const pageSettings = reactive([100, 200, 500])
    const radioButtons = reactive([
      { key: 'all', title: 'All' },
      { key: 'complete', title: 'Complete' },
      { key: 'progress', title: 'In Progress' },
      { key: 'hold', title: 'On Hold' },
      { key: 'expired', title: 'Expired' }
    ])

    const changeItemsPerPage = (item) => {
      perPage.value = item
    }
    return {
      textFilter,
      dateRange,
      radioButtons,
      status,
      columns,
      pageSettings,
      perPage,
      changeItemsPerPage
    }
  }
}
</script>

