<template>
  <section class="grid-table">
    <Table
      :data="data"
      :columns="columns"
      class="grid-table__layout"
    >
      <template #column="{ column }">
        <span class="grid-table__column">{{ column.title }}</span>
      </template>
      <template #cell="{ item, column }">
        <component
          :is="column.component || 'span'"
          :type="'success'"
          class="grid-table__cell"
        >
          {{ item }}
        </component>
      </template>
    </Table>
    <Pagination
      v-model="page"
      :per-page="perPage"
      :total-count="totalCount"
      class="grid-table__pagination"
    />
  </section>
</template>

<script>
import Table from '@/components/Table/Table'
import { computed, defineAsyncComponent } from 'vue'
import { useStore } from 'vuex'
import Pagination from '@/components/UIKit/Pagination'

export default {
  name: 'GridTable',
  components: {
    Pagination,
    Table,
    Badge: defineAsyncComponent(() => import('@/components/UIKit/Badge'))
  },
  setup () {
    const store = useStore()
    const data = computed(() => store.getters['swaps/getSwaps'])
    const columns = computed(() => store.getters['swaps/getActiveColumns'])
    const perPage = computed(() => store.getters['swaps/getPerPage'])
    const totalCount = computed(() => store.getters['swaps/getTotalCount'])
    const page = computed({
      get: () => store.getters['swaps/getPage'],
      set: (val) => store.commit('swaps/SET_PAGE', val)
    })

    return {
      data,
      columns,
      page,
      perPage,
      totalCount
    }
  }
}
</script>

