import { data as swapsMock, columns as columnsMock } from '@/store/mock/swapsMock'

export default {
  namespaced: true,
  state: {
    swaps: swapsMock,
    columns: columnsMock,
    filters: {
      dateRange: {
        from: '',
        to: ''
      },
      search: '',
      status: ''
    },
    perPage: 100,
    totalCount: 12300,
    page: 1
  },

  mutations: {
    SET_SWAPS (state, payload) {
      state.sendCoin = payload
    },
    SET_COLUMNS (state, payload) {
      state.receiveCoin = payload
    },
    SET_FILTERS (state, payload) {
      state.filters = payload
    },
    SET_PER_PAGE (state, payload) {
      state.perPage = payload
    },
    SET_TOTAL_COUNT (state, payload) {
      state.totalCount = payload
    },
    SET_PAGE (state, payload) {
      state.page = payload
    }
  },

  actions: {
    setSendCoin ({ commit }, payload) {
      commit('SET_SEND_COIN', payload)
    },
    setReceiveCoin ({ commit }, payload) {
      commit('SET_RECEIVE_COIN', payload)
    }
  },

  getters: {
    getSwaps (state) {
      return state.swaps
    },
    getColumns (state) {
      return state.columns
    },
    getActiveColumns (state) {
      return state.columns.filter(item => item.isShow)
    },
    getPerPage (state) {
      return state.perPage
    },
    getFilters (state) {
      return state.filters
    },
    getPage (state) {
      return state.page
    },
    getTotalCount (state) {
      return state.totalCount
    }
  }
}
